.StylizedButton {
  margin: 5px;
  padding: 10px;
  border: 3px solid var(--accent-color);
  color: var(--accent-color);
  border-radius: 5px;
  background-color: rgba(0,0,0,0);
  font-family: var(--main-font);
  transition: all 0.3s ease;
  cursor: pointer;
}

.StylizedButton:hover {
  background-color: var(--accent-color-low-opacity);
}